<template>
  <div class="bodySize">
    <IndexCanvasBg></IndexCanvasBg>
    <Index-Header></Index-Header>
    <Index-Knowledge :levels="levels"></Index-Knowledge>
    <Index-ArticleList :articleList="articleList"></Index-ArticleList>
  </div>
</template>

<script>
import IndexHeader from './components/Header'
import IndexKnowledge from './components/Knowledge'
import IndexArticleList from './components/ArticleList'
import IndexCanvasBg from './components/CanvasBg'
import axios from 'axios'

export default {
  name: 'index',
  components: {
    IndexHeader,
    IndexKnowledge,
    IndexArticleList,
    IndexCanvasBg
  },
  data () {
    return {
      levels: [],
      articleList: []
    }
  },
  methods: {
    getHomeInfo () {
      // axios.get('/api/primaryKnowledge.json').then(this.handleGetCityInfoSucc)
      axios.get('https://leedeea.github.io/Online-website/' + './static/mock/primaryKnowledage.json').then(this.handleGetKnowleageInfoSucc)
    },
    handleGetKnowleageInfoSucc (res) {
      res = res.data
      // console.log(res.data)
      this.levels = res.data.knowledge
      this.articleList = res.data.articleList
      // console.log(this.articleList)
    }
  },
  mounted () {
    this.getHomeInfo()
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  font-size: 16px;
}

ul li {
  list-style-type: none;
}
</style>



